import user from '../../api/user';

const state = {
  userInfo: {
    id: null,
    nickname: '',
    email: '',
    cover: '',
    website: '',
    introduction: ''
  }
};

const mutations = {
  SET_USER_INFO(state, data) {
    state.userInfo = data
  }
};

const actions = {

  // 创建用户
  async createUser({state,commit}, params){
    return await user.registerUser(params)
  },

  // 登录
  async userLogin({state,commit}, params){
    return await user.login(params)
  },

  // 获取当前用户信息
  async getUserInfo({state,commit}, params){
    const res = await user.info(params);
    commit('SET_USER_INFO', res.data.data);
    return res;
  },

  // 更新用户
  async updateUser({state, commit}, params) {
    return user.update(params);
  },

  // 用户签到
  async userAttendance({state, commit}, params) {
    return user.attendance(params);
  },
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



