import fetch from './fetch';

export default {
  // 注册用户
  registerUser(params) {
    return fetch.post('/user', params);
  },
  // 登录
  login(params) {
    return fetch.post('/user/login', params)
  },
  // 获取用户信息
  info(params) {
    return fetch.get('/user/info', params)
  },
  // 更新用户
  update(params) {
    const {id} = params;
    delete params.id;
    delete params.email;
    return fetch.put('/user/' + id, params)
  },
  // 用户签到
  attendance(params) {
    const {id} = params;
    delete params.id;
    delete params.email;
    return fetch.put('/user/attendance/' + id, params)
  },
}



