/**
 *   @description 对 axios 的一些处理，比如登录鉴权，请求拦截等等
 *   @author Dave
 */

import Util from '../libs/util';
import qs from 'qs';
import Vue from 'vue';
import store from '../store/index';
import {Base64} from 'js-base64';

// 设置 request 请求方式为 ajax 异步请求
Util.ajax.defaults.headers.common = {
  'X-Requested-With': 'XMLHttpRequest'
};

// 编码加密 token
function _encode() {
  const token = Vue.ls.get("token");
  const base64 = Base64.encode(token + ':');
  return 'Basic ' + base64;
}

/**
 * 关闭loading
 */
function closeLoading() {
  // 延迟100毫秒关闭
  setTimeout(() => {
    // 关闭loading
    store.dispatch('loading/closeLoading')
  }, 100)
}

// 设置请求拦截器
Util.ajax.interceptors.request.use(config => {
  if (config.isLoading) {
    // 开启loading
    store.dispatch('loading/openLoading')
  }
  // 给 headers 头部增加鉴权 token 字段
  if (_encode()) {
    config.headers.Authorization = _encode();
  }
  return config;
}, error => {
  return Promise.reject(error)
});

// 设置返回拦截器
Util.ajax.interceptors.response.use(response => {
  // 关闭loading
  closeLoading();
  return response;
}, error => {
  const status = error.response.status;
  let errMsg = error.response.data.msg;
  Vue.prototype.$message({message: errMsg, type: 'error'});
  // 关闭loading
  closeLoading();
  if (status === 401 || status === 403) {
    console.log('status', status);
    // Vue.$router.push('/');
    window.location.href = '/';
    // 清除用户信息
    store.commit('user/SET_USER_INFO', null);
    // 删除 token
    Vue.ls.remove('token');
    /*if (error.response.data.request !== 'GET /v1/user/info') {
      store.commit('user/SHOW_USER_MANAGER_MODEL', true);
    }*/
  }
  return Promise.reject(error);

});

export default {
  post(url, params = {}) {
    const {username} = params;
    const {isLoading = true} = params;

    return Util.ajax({
      method: 'post',
      url: url,
      data: qs.stringify(params),
      timeout: 30000,
      auth: {
        username
      },
      isLoading,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      }
    })
  },

  get(url, params = {}) {
    const {username} = params;
    const {isLoading = true} = params;

    return Util.ajax({
      method: 'get',
      url: url,
      params,
      auth: {
        username
      },
      isLoading
    })
  },

  delete(url, params = {}) {
    let {isLoading = true} = params;
    return Util.ajax({
      method: 'delete',
      url: url,
      params,
      isLoading
    })
  },

  put(url, params = {}) {
    let {isLoading = true} = params;
    return Util.ajax({
      method: 'put',
      url: url,
      data: qs.stringify(params),
      isLoading,
      timeout: 30000,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      }
    })
  }
}



