<template>
  <div>
    <div class="register">
      <template v-if="!showHeadImg">
        <a class="register-btn" @click="openDialog('registerForm')">注册</a>
        <a @click="openDialog('loginForm')">登录</a>
      </template>
      <el-dropdown trigger="click" v-if="showHeadImg" @command="toPage">
        <img class="head-img" v-if="userInfo.cover" :src="userInfo.cover" alt="头像">
        <img class="head-img" v-else src="../assets/images/default-img.png" alt="头像">
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item icon="el-icon-setting" command="/u">设置</el-dropdown-item>
          <el-dropdown-item icon="el-icon-s-promotion" divided command="attendance" :disabled="userInfo.attendance_count === 0">{{userInfo.attendance_count !== 0?'签到':'已签到'}}</el-dropdown-item>
          <el-dropdown-item icon="el-icon-link" divided command="logout">登出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <el-dialog :title="dialogTitle" :visible.sync="dialogTableVisible" width="320px">
      <el-form v-show="formVisible === 'registerForm'" :model="registerForm" status-icon :rules="rules" ref="registerForm">
        <el-form-item prop="nickname">
          <el-input v-model="registerForm.nickname" placeholder="请输入昵称"></el-input>
        </el-form-item>
        <el-form-item prop="email">
          <el-input v-model="registerForm.email" placeholder="请输入邮箱"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input type="password" v-model="registerForm.password" autocomplete="off"
                    placeholder="请输入密码（不少于6位）"></el-input>
        </el-form-item>
        <el-form-item prop="checkPass">
          <el-input type="password" v-model="registerForm.checkPass" autocomplete="off" placeholder="请确认密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary" @click="createUsers('registerForm')">注册</el-button>
          <el-button class="login-btn" type="text" @click="openDialog('loginForm')">已有账号登录</el-button>
        </el-form-item>
      </el-form>
      <el-form v-show="formVisible === 'loginForm'" :model="loginForm" status-icon :rules="loginFormRules"
               ref="loginForm">
        <el-form-item prop="lEmail">
          <el-input v-model="loginForm.lEmail" placeholder="请输入邮箱"></el-input>
        </el-form-item>
        <el-form-item prop="lPassword">
          <el-input type="password" v-model="loginForm.lPassword" autocomplete="off" placeholder="请输入密码"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary" @click="submitForm('loginForm')">登录</el-button>
          <el-button class="login-btn" type="text" @click="openDialog('registerForm')">没有账号? 注册</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';
  import store from '../store/index';
  // 默认头像
  const headImg = import('../assets/images/default-img.png');
  export default {
    data() {
      let checkNickname = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('昵称不能为空'));
        }
        callback();
      };
      let checkEmail = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('邮箱不能为空'));
        }
        callback();
      };
      let checkPassword = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入密码'));
        }
        callback();
      };
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.registerForm.checkPass !== '') {
            this.$refs.registerForm.validateField('checkPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.registerForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        // 是否显示头像
        showHeadImg: !!this.$ls.get('token'),
        headImg: headImg,
        registerForm: {
          password: '',
          checkPass: '',
          nickname: '',
          email: ''
        },
        rules: {
          password: [
            {validator: validatePass, trigger: 'blur'}
          ],
          checkPass: [
            {validator: validatePass2, trigger: 'blur'}
          ],
          nickname: [
            {validator: checkNickname, trigger: 'blur'}
          ],
          email: [
            {validator: checkEmail, trigger: 'blur'}
          ]
        },
        dialogTitle: '注册',
        dialogTableVisible: false,
        // 默认显示注册表单
        formVisible: 'registerForm',
        loginForm: {
          lEmail: '',
          lPassword: ''
        },
        loginFormRules: {
          lEmail: [
            {validator: checkEmail, trigger: 'blur'}
          ],
          lPassword: [
            {validator: checkPassword, trigger: 'blur'}
          ],
        }
      }
    },
    computed: {
      ...mapState({
        // 获取当前登录用户相关信息
        userInfo: state => state.user.userInfo
      })
    },
    methods: {
      ...mapActions({
        createUser: 'user/createUser',
        userLogin: 'user/userLogin',
        getUserInfo: 'user/getUserInfo',
        userAttendance: 'user/userAttendance',    // 签到
      }),
      // 路由跳转
      toPage(path) {
        if (path === 'logout') {
          this.showHeadImg = false;
          this.$ls.remove('token');
          this.$router.push('/');
          // 清除用户信息
          store.commit('user/SET_USER_INFO', null);
        } else if (path === 'attendance') {
          this.attendanceEvent();
        } else {
          this.$router.push(path);
        }
      },
      // 签到 channelId:积分变动渠道id（1签到 2评论）
      async attendanceEvent() {
        let res = await this.userAttendance({id: this.userInfo.id, channelId: 1});
        if (res.data.code === 200) {
          this.$message({message: `签到成功！奖励 ${res.data.data.integral} 个积分`, type: 'success'});
          await this.getUserInfo({username: this.$ls.get('token')});
        } else {
          this.$message({message: '签到失败！', type: 'error'});
        }
      },
      /**
       *  提交注册表单
       */
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.login();
          } else {
            return false;
          }
        });
      },
      /**
       *  重置表单
       */
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      /**
       *  创建用户
       */
      async createUsers() {
        let {password, nickname, email} = this.registerForm;
        await this.createUser({email, nickname, password1: password, password2: password});
        this.$message({message: '注册成功，请登录！', type: 'success'});
        this.openDialog('loginForm');
      },
      /**
       *  登录
       */
      async login() {
        let {lEmail, lPassword} = this.loginForm;
        let res = await this.userLogin({email: lEmail, password: lPassword});
        if (res.data.token) {
          this.$ls.set('token', res.data.token);
          this.$message({message: '登录成功！', type: 'success'});
          this.dialogTableVisible = false;
          this.showHeadImg = true;
          this.$router.push('/u');
        }
      },
      /**
       *  打开登录/注册 弹窗
       */
      openDialog(type) {
        this.formVisible = type;
        this.dialogTableVisible = true;
        if (type === 'registerForm') {
          this.dialogTitle = '注册';
          if (this.$refs['loginForm']) {
            this.resetForm('loginForm');
          }
        } else {
          this.dialogTitle = '登录';
          if (this.$refs['registerForm']) {
            this.resetForm('registerForm');
          }
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .register {
    position: relative;
    margin-left: 30px;

    a {
      font-size: 20px;
      color: #409EFF;
      margin-left: 16px;
      cursor: pointer;
    }

    a.register-btn {
      &::after {
        content: '.';
        position: absolute;
        top: -3px;
        left: 63px;
      }
    }
  }

  .login-btn {
    width: 100%;
    margin-left: 0;
  }

  .head-img {
    width: 50px;
    border-radius: 50%;
    border: 1px solid #f3efef;
    cursor: pointer;
  }
</style>



