import Vue from 'vue';
import App from './App';
import router from './router';
import store from './store';
import VueLocalStorage from 'vue-ls';
import 'lib-flexible/flexible';
import VueLazyLoad from 'vue-lazyload';
import mavonEditor from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import './assets/style/index.scss';
import filters from './filters';
import {
  Upload,
  Button,
  Input,
  Form,
  FormItem,
  Message,
  Tabs,
  TabPane,
  Dialog,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Pagination,
  Tag,
  Icon,
  Row,
  Col,
  Table,
  TableColumn
} from 'element-ui';

Vue.use(Button);
Vue.use(Input);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Dialog);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Pagination);
Vue.use(Tag);
Vue.use(Icon);
Vue.use(Upload);
Vue.use(Row);
Vue.use(Col);
Vue.use(Table);
Vue.use(TableColumn);

// 注册全局过滤器
for (let key in filters) {
  Vue.filter(key, filters[key]);
}

Vue.prototype.$message = Message;

Vue.config.productionTip = false;
Vue.use(mavonEditor);
Vue.use(VueLocalStorage, {
  namespace: 'v-blog-'
});
Vue.use(VueLazyLoad, {
  error: '../static/boblog.png',
  loading: '../static/boblog.png'
});

router.beforeEach(async (to, from, next) => {
  // 获取用户信息
  let token = Vue.ls.get("token");
  if (token) {
    const auth = {username: token};
    try {
      await store.dispatch('user/getUserInfo', auth);
      await next()
    } catch (e) {
      await next()
    }
    await next()
  } else {
    // 判断是否需要登录
    if (to.meta.isLogin) {
      await next('/');
    } else {
      await next();
    }
  }
});

router.afterEach(() => {

});

new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
});



