import moment from "moment";

/**
 *   @description 过滤器
 *   @param
 *   @time
 *   @author
 */

const filters = {
  // 日期处理
  dateFormat: (dataStr, pattern = 'YYYY-MM-DD HH:mm:ss') => {
    return moment(dataStr).format(pattern);
  },
  // 积分来源
  integralSource: (type) => {
    if (type === 1) {
      return '签到';
    } else if (type === 2) {
      return '评论';
    }
  }
};

export default filters;


